<template>
    <BlockContent :title="name">
        <div class="loading" v-if="loading">Now loading...</div>
        <div class="error" v-if="error">{{ error }}</div>
        <div v-if="user">
            <h2>{{ user.name }}</h2>
            <p>{{ user.description }}</p>
        </div>
    </BlockContent>
</template>

<script>
import User from '../User.js'
import BlockContent from "@/components/common/BlockContent.vue"

export default {
    name: 'UserDetail',
    components: {
        BlockContent
    },
    data: function() {
        return {
            loading: false,
            name: 'loading',
            user: null,
            error: null
        }
    },
    created: function() {
        this.fetchData()
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData: function() {
            this.loading = true
            User.getUser(this.$route.params.userid, (function(err, user) {
                this.loading = false
                if (err) {
                    this.error = err.toString()
                } else {
                    this.user = user
                    this.name = user.name
                }
            }).bind(this))
        }
    }
}
</script>