<template>
    <BlockContent title="UserList">
        <div class="loading" v-if="loading">Now loading...</div>
        <div v-if="error" class="error">
            {{ error }}
        </div>
        <div class="table-responsive">
            <table class="table table-hover">
                <caption>UserList</caption>
                <thead>
                    <tr>
                        <th scope="col">ID</th>
                        <th scope="col">Name</th>
                        <th scope="col">Button</th>
                    </tr>
                    <tr v-for="user in users" :key="user.id">
                        <th scope="row">{{ user.id }}</th>
                        <td>{{ user.name }}</td>
                        <td><router-link class="btn btn-secondary" :to="{path: '/users/' + user.id}">Detail</router-link></td>
                    </tr>
                </thead>
            </table>
        </div>
    </BlockContent>
</template>

<script>
import User from '../User.js'
import BlockContent from "@/components/common/BlockContent.vue"

export default {
    name: 'UserList',
    components: {
        BlockContent
    },
    data: function() {
        return {
            loading: false,
            users: function() { return [] },
            error: null
        }
    },
    created: function() {
        this.fetchData()
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData: function() {
            this.loading = true
            User.getUsers((function(err, users) {
                this.loading =false
                if (err) {
                    this.error = err.toString()
                } else {
                    this.users = users
                }
            }).bind(this))
        }
    }
}
</script>