import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import AdminRoutes from '@/package/admin/admin-routes'
import BoardRoutes from '@/package/sampleboard/board-routes'
import BlogRoutes from '@/package/blog/blog-routes'
import CampRoutes from '@/package/camp/camp-routes'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    ...AdminRoutes
  },
  {
    ...BlogRoutes
  },
  {
    ...BoardRoutes
  },
  {
    ...CampRoutes
  }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

export default router
