<template>
    <section class="container">
        <h1>{{ title }}</h1>
        <p>{{ message }}</p>
    </section>
</template>

<script>
import firebase from 'firebase/app';
import 'firebase/auth';

export default {
    name: 'SocialLogin',
    data: function() {
        return {
            title: 'Auth',
            message: 'this is message.'
        }
    },
    created: function() {
        var firebaseConfig = {
            apiKey: "AIzaSyBKOQiS8OxgNeZafMN3htzBPyQu80VG9k4",
            authDomain: "github-io-283610.firebaseapp.com",
            databaseURL: "https://github-io-283610.firebaseio.com",
            projectId: "github-io-283610",
            storageBucket: "github-io-283610.appspot.com",
            messagingSenderId: "1019518338004",
            appId: "1:1019518338004:web:f162987b2aff4ef5d6cc41",
            measurementId: "G-GSDM2R08LZ"
        }

        firebase.initializeApp(firebaseConfig)

        var provider = new firebase.auth.GoogleAuthProvider()
        var self = this
        firebase.auth().signInWithPopup(provider)
            .then(function(result) {
                self.message = result.user.displayName + ', '
                    + result.user.email
            })
    }
}
</script>