import UserIndex from '@/package/admin/users/views/UserIndex.vue'
import UserList from '@/package/admin/users/views/UserList.vue'
import UserCreate from '@/package/admin/users/views/UserCreate.vue'
import UserDetail from '@/package/admin/users/views/UserDetail.vue'

export default {
  path: '/users',
  component: UserIndex,
  children: [
    {
      path: '',
      component: UserList
    },
    {
      path: 'new',
      component: UserCreate
    },
    {
      path: ':userid',
      component: UserDetail
    }
]
}